import * as moment from 'moment';

import * as _moment from 'moment-timezone/builds/moment-timezone-with-data.min'

Vue.filter('momenttz', function (value) { 
    if (!value) return ''
    return moment(value).tz("America/New_York");
})

Vue.filter('momenttzMDY', function (value) { 
    if (!value) return ''
    return moment(value).tz("America/New_York").format("MM/DD/YYYY");
})

Vue.filter('momentDayTime', function (value) { 
    if (!value) return ''
    return moment.utc(value).local().format("MM/DD/YYYY, h:mm A");
})

